<template>
    <div class="container row">
      <main class="container col">
        <br>
        <h1 class="display-6 text-center">Gachi++에 오신것을 환영합니다!
        </h1>
        <hr>
        <article >
          <p class="lead">
            "가치++는 누구든 시간과 노력을 들여 자신이 정한 목표를 이룰 수 있도록 도와주는 소셜 네트워크 입니다. 
            개인의 목표가 우리의 목표가 되어 더 큰 가치를 만들어 낼 수 있습니다."
            </p>
            <br>
          <figure class="text-end">
            <blockquote class="blockquote">
              <p class="lead fs-6"> "성공한 사람이 아니라 가치있는 사람이 되기 위해 힘쓰라" <br>
              <span class="fs-7">"Try not to become a man of success but rather try to become a man of value"</span></p>
            </blockquote>
            <figcaption class="blockquote-footer">
              알버트 아인슈타인<cite title="Source Title">Albert Einstein</cite>
            </figcaption>
          </figure>
        </article>
      </main>

    <div class="col tuto position-relative" >
        <RouterLink to="/" style="text-decoration:none; color:#30353d;" >
            <button type="button" class="btn btn-light btn-lg position-absolute top-0 end-0" style="background:rgba(6, 90, 92, 0.3);border-radius: 2em;">X</button>
        </RouterLink>
        <h3>Gachi는 당신의 목표를 더 잘 이룰 수 있도록 아젠다를 통해 도와줍니다.</h3>
        <table class="table">
            <thead>
            <tr>
                <th scope="col"></th>
                <th scope="col">할 일</th>
                <th scope="col">목표</th>
                <th scope="col">상태</th>
                <th scope="col"></th>
            </tr>
            </thead>
            <tbody v-for="(list, i) in state.lists" :key="(list, i)">
            <tr v-if="list.dates != state.tmpDayNb" class="table-success">
                <th scope="row">{{list.dates.substring(5)}} </th>
                <td>{{list.goalName}}</td>
                <td>{{list.qgoal}} {{list.unit}}</td>
                <td>{{list.qdone}} {{list.unit}}</td>
                <span hidden> {{ state.tmpDayNb = list.dates }}</span>
                <td>
                <button class="btn btn-outline-success" data-bs-toggle="tooltip" data-bs-placement="top"
                    title="to complete the task" @click="beDone(i)">✓</button>
                <button class="btn btn-outline-light" data-bs-toggle="tooltip" data-bs-placement="top"
                    title="delete the task for ever" @click="deleteEntry(list)">X</button>
                </td>
            </tr>
            <tr v-else>
                <span hidden> {{ state.tmpDayNb = list.dates }}</span>
                <th scope="row"></th>
                <td>{{list.goalName}}</td>
                <td>{{list.qgoal}} {{list.unit}}</td>
                <td>{{list.qdone}} {{list.unit}}</td>
                <td>
                <button class="btn btn-outline-success" data-bs-toggle="tooltip" data-bs-placement="top"
                    title="to complete the task" @click="beDone(i)">✓</button>
                <button class="btn btn-outline-light" data-bs-toggle="tooltip" data-bs-placement="top"
                    title="delete the task for ever" @click="deleteEntry(list)">X</button>
                </td>
            </tr>
            </tbody>
        </table>
        <div class="mb-3 item buttons col-md-6 col-sm-6" style="margin-left:150px" > 
            <RouterLink to="/tutoCircle" style="text-decoration:none; color:#30353d;" >
            <button type="button" class="btn btn-success" style="background:rgba(6, 90, 92, 0.5);border-radius: 2em;">
                <svg xmlns="http://www.w3.org/2000/svg" width="50" height="50" fill="currentColor" class="bi bi-arrow-return-right" viewBox="0 0 16 16">
                    <path fill-rule="evenodd" d="M1.5 1.5A.5.5 0 0 0 1 2v4.8a2.5 2.5 0 0 0 2.5 2.5h9.793l-3.347 3.346a.5.5 0 0 0 .708.708l4.2-4.2a.5.5 0 0 0 0-.708l-4-4a.5.5 0 0 0-.708.708L13.293 8.3H3.5A1.5 1.5 0 0 1 2 6.8V2a.5.5 0 0 0-.5-.5z"></path>
                </svg> <span class="fs-1">더 보기</span> 
            </button>
        </RouterLink>
        </div>
            <Teleport to="body">
                <modal2 :show="state.showModal" @close="state.showModal=false">
                <template #header>
                    <h2>
                      <div class="snowflakes" aria-hidden="true">
                        <div class="snowflake">            ❅            </div>
                        <div class="snowflake">            ❅            </div>
                        <div class="snowflake">            ❆            </div>
                        <div class="snowflake">            ❄            </div>
                        <div class="snowflake">            ❅            </div>
                        <div class="snowflake">            ❆            </div>
                        <div class="snowflake">            ❄            </div>
                        <div class="snowflake">            ❅            </div>
                        <div class="snowflake">            ❆            </div>
                        <div class="snowflake">            ❄            </div>
                      </div>
                    </h2>
                </template>
                <template #body>
                  <img style="width:55em" alt="user icon" src="../assets/images/pictures/congraturations3.jpg">
                  <div>
                    <h5>축하합니다!! 할 일을 완료 하였습니다!!</h5>
                  </div>
                </template>
                <template #footer>
                    <el-button type="primary" plain @click="btnCon">Thank you</el-button>
                </template>
                </modal2>
            </Teleport>
    </div>
</div>
</template>
<script>
    import { reactive,computed  } from 'vue';
    import axios from 'axios';
    import { onMounted } from 'vue'
    import Modal2 from '../components/teleport/Modal2.vue'
    import { useRouter } from 'vue-router';
    import { Tooltip } from 'bootstrap/dist/js/bootstrap.esm.min.js'
    export default {
      data() {
        
        let tmpDayNbData = "20";
        return { tmpDayNbData: 20 };
      },
      computed:{
        colorIfDone(listIndex){
        }
      },
      mounted() {
        //inti tooltip
        Array.from(document.querySelectorAll('button[data-bs-toggle="tooltip"]'))
          .forEach(tooltipNode => new Tooltip(tooltipNode))
      },
      setup() {
        
        const router = useRouter();
        onMounted(() => {
          getData()
        })
        const state = reactive({
          lists: "",
          showModal: false,
          email: sessionStorage.getItem("email"),
          token: sessionStorage.getItem("TOKEN"),
        });
        const getData = async () => {
          console.log("loginHandler launch");
          const url = "http://localhost:9090/api/calendarlistbyuid";

          const headers = {
            "Content-Type": "application/json",
            "Authorization": state.token,
            "token": state.token,
            "test": "test",
          };
          // get receive the userId on the controller.
          const body = { email: 'hyelan@ds.org' };
          await axios.post(url, body, { headers }).then(function (res) {
            if (res.data != null) {
              state.lists = res.data;
            }
            else {
              alert("bad.");
            }
          });
        };
        const beDone = async (i) => {
          console.log("beDone launch");
          const url = "http://localhost:9090/api/beDone";

          const headers = {
            "Content-Type": "application/json",
            "Authorization": state.token,
            "token": state.token,
            "test": "test",
          };
          const body = { calendarid: state.lists[i].calendarid };
          await axios.post(url, body, { headers }).then(function (res) {
            if (res.status == 200) {
              state.showModal = true;
            }
          });
        }
        const deleteEntry = async (list) => {
    
          const url = "http://localhost:9090/api/deleteCalendarEntry"
    
          const headers = {
            'Content-Type': 'application/json',
            Authorization: sessionStorage.getItem('TOKEN'),
            token: sessionStorage.getItem('TOKEN'),
            'name': 'test list'
          }
          //seems to send a whole Calendar DTO and not just the calendarid...
          const body = { calendarid: list.calendarid }
          await axios.post(url, body, { headers }).then(function (res) {
            if (res.status == 200) {
              setTimeout(getData(), 1000)
            } else {
              alert('bad.')
            }
          }).catch(function (error) {
            //TODO: If any error send the same message.
            //could be more precise... (like 500?)
            if (error) {
              alert('something went wrong..')
              console.log('Error', error.message);
            }
          })
        }
        const btnCon = () => {
          state.showModal = false;
          setTimeout(getData(), 1000)
          // router.go(0);
          // window.location.reload();
        };
        return { state,  getData, beDone, btnCon, deleteEntry};
      },
      components: {      Modal2,    },
    }
    </script >
      

<style scoped>
.tuto {
	background: linear-gradient(-45deg, #ee7752, #e73c7e, #23a6d5, #23d5ab);
	/* background: linear-gradient(-45deg, #065A5c, #D9D9D9); */
	background-size: 200% 200%;
	animation: gradient 10s ease infinite;
  border-radius: 1em;
  padding: 3em;
  color: aliceblue;
}

@keyframes gradient {
	0% {
		background-position: 0% 50%;
	}
	50% {
		background-position: 100% 50%;
	}
	100% {
		background-position: 0% 50%;
	}
}
.tooltip {
  position: relative;
  display: inline-block;
  border-bottom: 1px dotted black;
}

.tooltip .tooltiptext {
  visibility: hidden;
  width: 120px;
  background-color: black;
  color: #fff;
  text-align: center;
  border-radius: 6px;
  padding: 5px 0;

  /* Position the tooltip */
  position: absolute;
  z-index: 1;
}

.tooltip:hover .tooltiptext {
  visibility: visible;
}
@-webkit-keyframes snowflakes-fall{0%{top:-10%}100%{top:100%}}
@-webkit-keyframes snowflakes-shake{0%{-webkit-transform:translateX(0px);
  transform:translateX(0px)}50%{-webkit-transform:translateX(80px);
    transform:translateX(80px)}100%{-webkit-transform:translateX(0px);
    transform:translateX(0px)}}
@keyframes snowflakes-fall{0%{top:-10%}100%{top:100%}}
@keyframes snowflakes-shake{0%{transform:translateX(0px)}50%{transform:translateX(80px)}100%{transform:translateX(0px)}}
.snowflake{
  position:fixed;
  top:-10%;z-index:9999;
  -webkit-user-select:none;
  -moz-user-select:none;
  -ms-user-select:none;
  user-select:none;cursor:default;
  -webkit-animation-name:snowflakes-fall,snowflakes-shake;
  -webkit-animation-duration:10s,3s;
  -webkit-animation-timing-function:linear,ease-in-out;
  -webkit-animation-iteration-count:infinite,infinite;
  -webkit-animation-play-state:running,running;
  animation-name:snowflakes-fall,snowflakes-shake;
  animation-duration:10s,3s;
  animation-timing-function:linear,ease-in-out;animation-iteration-count:infinite,infinite;
  animation-play-state:running,running}
  .snowflake:nth-of-type(0){left:1%;-webkit-animation-delay:0s,0s;animation-delay:0s,0s}
  .snowflake:nth-of-type(1){left:10%;-webkit-animation-delay:1s,1s;animation-delay:1s,1s}
  .snowflake:nth-of-type(2){left:20%;-webkit-animation-delay:6s,.5s;animation-delay:6s,.5s}
  .snowflake:nth-of-type(3){left:30%;-webkit-animation-delay:4s,2s;animation-delay:4s,2s}
  .snowflake:nth-of-type(4){left:40%;-webkit-animation-delay:2s,2s;animation-delay:2s,2s}
  .snowflake:nth-of-type(5){left:50%;-webkit-animation-delay:8s,3s;animation-delay:8s,3s}
  .snowflake:nth-of-type(6){left:60%;-webkit-animation-delay:6s,2s;animation-delay:6s,2s}
  .snowflake:nth-of-type(7){left:70%;-webkit-animation-delay:2.5s,1s;animation-delay:2.5s,1s}
  .snowflake:nth-of-type(8){left:80%;-webkit-animation-delay:1s,0s;animation-delay:1s,0s}
  .snowflake:nth-of-type(9){left:90%;-webkit-animation-delay:3s,1.5s;animation-delay:3s,1.5s}

